import * as React from 'react';
import { authProvider } from '../utils/auth';
import { AuthContext } from '../hooks/auth';

export const AuthProvider = function AuthProvider({ children }: { children: React.ReactNode }) {
  let [user, setUser] = React.useState<{ email: string, password: string } | null>(null);

  let signin = (email: string, password: string, callback: (res: boolean) => void) => {
    return authProvider.signin(email, password, (res) => {
      setUser({ email, password });
      callback(res);
    });
  };

  let signout = (callback: VoidFunction) => {
    return authProvider.signout(() => {
      localStorage.removeItem("user");
      setUser(null);
      callback();
    });
  };

  let value = { user, signin, signout };

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
}
