import * as React from 'react';
import {
  Routes,
  Route,
  useLocation,
  Navigate,
} from 'react-router-dom';

import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';

import Login from './login/Login';
import { useAuth } from './hooks/auth';
import { AuthProvider } from './providers/auth';
import Layout from './layout/layout';

export default function App() {
  return (
    <AuthProvider>
      <Routes>
        <Route path="/login" element={<Login />} />
        <Route path="*" element={
          <RequireAuth>
            <Layout>
              <Routes>
                <Route
                    path="/trucks_nearby"
                    element={
                      <ProtectedPage />
                    }
                />
                <Route
                    path="/general_map"
                    element={
                      <ProtectedPage />
                    }
                />
                <Route
                    path="/loads"
                    element={
                      <ProtectedPage />
                    }
                />
                <Route
                    path="/trucks"
                    element={
                      <ProtectedPage />
                    }
                />
                <Route
                    path="/owners"
                    element={
                      <ProtectedPage />
                    }
                />
                <Route
                    path="/coordinators"
                    element={
                      <ProtectedPage />
                    }
                />
                <Route
                    path="/drivers"
                    element={
                      <ProtectedPage />
                    }
                />
                <Route
                    path="/locations"
                    element={
                      <ProtectedPage />
                    }
                />
                <Route
                    path="/users"
                    element={
                      <ProtectedPage />
                    }
                />
                <Route
                    path="*"
                    element={
                        <NotFoundPage />
                    }
                />
              </Routes>
            </Layout>
          </RequireAuth>
        } />
      </Routes>
    </AuthProvider>
  );
}

function RequireAuth({ children }: { children: JSX.Element }) {
  const auth = useAuth();
  const location = useLocation();

  if (!auth.user) {
      return <Navigate to="/login" state={{ from: location }} replace />;
  }
  return children;
}

